.registerOverlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(213, 223, 213, 0.557);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
}

.registerContent {
  background-color: #ffffff;
  padding: 40px;
  border-radius: 20px;
  width: 90%;
  max-width: 400px;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.1);
  text-align: center;
  position: relative;
  font-family: "Arial", sans-serif;
  color: #333;
}
.registerContent h2 {
  font-size: 26px;
  margin-bottom: 20px;
  color: #20b2aa;
  font-weight: 700;
  letter-spacing: 1px;
  text-transform: uppercase;
}
.registerContent form {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.registerContent form label {
  width: 100%;
  margin-bottom: 15px;
  font-size: 14px;
  font-weight: bold;
  color: #555;
}
.registerContent form input {
  width: 100%;
  padding: 12px;
  border: 1px solid #98fb98;
  border-radius: 10px;
  background-color: #f9f9f9;
  color: #333;
  font-size: 16px;
  transition: border-color 0.3s ease;
}
.registerContent form input:focus {
  border-color: #20b2aa;
  outline: none;
}
.registerContent form button {
  width: 100%;
  padding: 15px;
  margin-top: 20px;
  border: none;
  border-radius: 25px;
  background-color: #20b2aa;
  color: #fff;
  font-size: 16px;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 1px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}
.registerContent form button:hover {
  background-color: #3cb371;
}

.closeButton {
  background-color: transparent;
  border: none;
  font-size: 24px;
  color: #20b2aa;
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
  opacity: 0.8;
  transition: opacity 0.3s ease;
}
.closeButton:hover {
  opacity: 1;
}/*# sourceMappingURL=Register.module.css.map */